import React from "react";
import PropTypes from "prop-types";
import "./Movie.css"

function Movie({id,title,summary,rating,poster,year,genres}){
    return (
        <div className="movie">
        <img src={poster} alt={title} title={title}></img>
            <div className="movie_data">
                <h3 className="movie_title">{title}</h3>
                <h5 className="movie_year">{year} [ no:{id} ] </h5>
                <ul className="genres">
                    {genres.map((current,index)=>
                        <li key={index} className="genres_genre">{current}</li>
                    )}
                </ul>
                <p className="movie_summery">{summary.substr(0,300)+" ..."}</p>
            </div>
        </div>
    );
}
Movie.propTypes={
    id:PropTypes.number.isRequired,
    title:PropTypes.string.isRequired,
    summary:PropTypes.string.isRequired,
    rating:PropTypes.number.isRequired,
    poster:PropTypes.string.isRequired,
    year:PropTypes.number.isRequired,
    genres:PropTypes.arrayOf(PropTypes.string).isRequired
}
export default Movie;